import React from 'react';
import style from './villager.module.css';



class Villager extends React.Component {

    constructor(props) {
        super(props);
        this.state = {on: false};
        this.toggle = this.toggle.bind(this);
        }

    toggle(event) {
        this.setState({on: !this.state.on});
        if (!this.state.on){
            document.documentElement.style.overflow = 'hidden';
        } else{
            document.documentElement.style.overflow = 'scroll';
        }
    }
    


    render(){
        return (
            <div className = {style.villager} onClick={this.toggle} style = {this.props.gender === "\u2642" ? {border: '2px solid rgb(94, 175, 221)'}: {border: '2px solid rgb(228, 143, 185)'}}>
                <img className = {style.image} src = {this.props.image} alt = ''/>
                <div className = {style.description}>
                    <div className = {style.title}>
                        <h2 className = {style.name}>{this.props.name}</h2>
                        <h2 style = {this.props.gender === "\u2642" ? {color: 'rgb(94, 175, 221)'}: {color:'rgb(228, 143, 185)'}}>{this.props.gender}</h2>
                    </div>
                    <h4>{this.props.personality}</h4>
                </div>

                <div style = {{display: this.state.on ? 'flex': 'none'}} className={style.extra}>
                    <div className = {style.bigimg}>
                        <img src = {this.props.image} alt = ''/>
                    </div>
                    <div>
                        <h2>{this.props.name}</h2>
                        <div className={style.species}>
                            <h4>{this.props.species}</h4>
                            <h4 className={style.gender} style = {this.props.gender === "\u2642" ? {color: 'rgb(94, 175, 221)'}: {color:'rgb(228, 143, 185)'}}>{this.props.gender}</h4>
                        </div>
                        <h4>{this.props.birthday}</h4>
                        <h4>({this.props.zodiac})</h4>
                        <ul>
                            {this.props.newleaf && <li style = {{color: 'rgb(49, 105, 49)'}}>NL</li>}
                            {this.props.wildworld && <li style = {{color: 'rgb(248, 248, 248)'}}>WW</li>}
                        </ul> 
                    </div>
                    <div className = {style.info}>
                        <div>
                            <div>Personality</div>
                            <div>{this.props.personality}</div> 
                        </div>
                        <div>
                            <div>Phrase</div>
                            <div>{this.props.phrase}</div> 
                        </div>
                        <div>
                            <div>Clothes</div>
                            <span className = {style.clothes}>{this.props.clothes}</span>
                        </div>
                        <div>
                            <div>Song</div>
                            <div>{this.props.song}</div>
                        </div>
                        <div>
                            <div>Skill</div>
                            <div>{this.props.skill}</div>
                        </div>
                        <div>
                            <div>Goal</div>
                            <div>{this.props.goal}</div>
                        </div>

                    </div>
                </div>
                
            </div>
        )
    }
}

export default Villager;