import React, {useEffect, useState, useRef} from 'react';
import Villagers from './Villagers'
import Villager from './Villager'
import './App.css';

function App() {
  
  const villagers = Object.keys(Villagers)
  
  const [query, setQuery] = useState('');
  const [results, setResults] = useState([]);
  const closeRef = useRef(null);
  const [villagerList, setVillagerList] = useState([]);
  const [display, setDisplay] = useState(false)

  const updateQuery = x => {
    setQuery(x.target.value);
  }

  useEffect(() => {
    const temp = Object.keys(Villagers);
    let suggest = [];
    if (query.trim().length > 0){
      let tmp = query.trim();
      tmp = tmp.replace(/[[\]]/g, "/\\");
      tmp = tmp.replace(/\\/g, "\\\\");
      const re = new RegExp(`^${tmp}`,'i');
      suggest = temp.filter(x => re.test(x));
    }
    setResults(suggest);}, [query]);
  
  useEffect(() => {
    document.addEventListener('mousedown',handleClickOutside);

    return () => {
      document.removeEventListener('mousedown',handleClickOutside);
    }
  }, [])

  const handleClickOutside = event => {
    const { current: wrap} = closeRef;
    if (wrap && !wrap.contains(event.target)){
      setDisplay(false)
    }
  }
  const lower = villagers.map(x => x.toLowerCase());

  const addVillager = x => {
    x.preventDefault();
    let tmp = query.replace(/  +/g, ' ');
    if (lower.includes(tmp.toLowerCase().trim())){
      const i = lower.findIndex(x => x === tmp.toLowerCase().trim());
      if (!villagerList.includes(villagers[i])){
        document.getElementsByClassName("alert")[0].textContent = '';
        setVillagerList(x => [...x,villagers[i]])
        setQuery('')
        let temp = villagerList.concat(villagers[i])
        sessionStorage.setItem('myACData', temp.join(','));
      } else {
        document.getElementsByClassName("alert")[0].textContent = villagers[i] + ' is already added!';
      }
    } else {
      if(query.trim()){
        document.getElementsByClassName("alert")[0].textContent = query + ' is not a villager';
      } else{
        document.getElementsByClassName("alert")[0].textContent = query + 'Enter a villager!';
      }
      
    }
  }

  useEffect(() => {
    let tmp = sessionStorage.getItem('myACData')
    if (tmp){
      setVillagerList(tmp.split(","))
    }
  }, []);

  const delvillager = (vill) => {
    let temp = villagerList.filter(x => x !== vill)
    setVillagerList([...temp])
    sessionStorage.setItem('myACData', temp.join(','));
}

  return (
    <div className="App">
      <img src = 'https://vignette.wikia.nocookie.net/ultimatepopculture/images/d/d0/Animal_Crossing_New_Horizons_logo.png/revision/latest?cb=20190922190539' alt ='Animal Crossing New Horizons'/>
      <div className = 'alert'></div>

       <form onSubmit = {addVillager}>
          <div>
            <input onClick={()=> {setDisplay(true)}} type = 'text' value = {query} onChange = {updateQuery} placeholder = 'Add a Villager'></input>
            <div id="villagers" className = "suggested" ref = {closeRef}>
            {display && results.map(villager => 
                <li key = {villager} onClick={()=> {setQuery(villager); setDisplay(false)}}>{villager}</li>
                )}
            </div>
          </div>
          <button>Add</button>
      </form>
      <div className = 'animals'>
        {villagerList.map(villager => {
        return (
          <div className="card" key = {villager}>
            <div style = {Villagers[villager].Gender === "\u2642" ? {border: '2px solid rgb(94, 175, 221)'}: {border: '2px solid rgb(228, 143, 185)'}} className='remove' onClick={() => delvillager(villager)}>&#x2715;</div>
            <Villager key = {villager} name = {villager} gender = {Villagers[villager].Gender} personality = {Villagers[villager].Personality} species = {Villagers[villager].Species}
            birthday = {Villagers[villager].Birthday} phrase = {Villagers[villager].Phrase} clothes = {Villagers[villager].Clothes} goal = {Villagers[villager].Goal}
            newleaf = {Villagers[villager].NewLeaf} skill = {Villagers[villager].Skill} song = {Villagers[villager].Song} wildworld = {Villagers[villager].WildWorld}
            zodiac = {Villagers[villager].Zodiac} image = {Villagers[villager].Image}></Villager>
          </div>
        )})}      
      </div>
      <div className='end'>
        <a target="_blank" rel="noopener noreferrer" href="https://animalcrossing.fandom.com/wiki/Animal_Crossing:_New_Horizons">Data obtained from Animal Crossing Fandom</a>
        <div>Updated: April 6,2020</div>
      </div>
    </div>
  );
}
export default App;
